import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login.vue'

Vue.use(Router)

export default new Router({
	base: process.env.BASE_URL,
	routes: [{
			path: '/',
			name: 'login',
			component: Login
		},
		{
			path: '/mains',
			name: 'mains',
			component: () => import('./views/Mains.vue'),
			meta: {
				title: '主页',
				isLogin: true
			},
			children: [{
					path: 'business',
					name: 'business',
					component: () => import('./views/Business/Business.vue'),
					meta: {
						title: '业务管理',
						icon: 'el-icon-yibiaopan1',
						isshow: true,
						isLogin: true
					},
					children:[{
							path: 'examines',
							name: 'examines',
							component: () => import('./views/Business/Examines.vue'),
							meta: {
								title: '订单详情',
								isshow: true,
								isLogin: true
							}
						}
					]

				},
				{
					path: 'form',
					name: 'form',
					component: () => import('./views/Form/Form.vue'),
					meta: {
						title: '统计报表',
						icon: 'el-icon-lingyong',
						isshow: true,
						isLogin: true
					},
					children:[
						{
							path: 'charge',
							name: 'charge',
							component: () => import('./views/Form/Charge.vue'),
							meta: {
								title: '设备收费统计',
								isshow: true,
								isLogin: true
							}
						}
					]
				}
			]
		}
	]
})
